@use "sass:math";

fieldset {
  background: transparent;
  border: $base-border;
  margin: 0 0 ($base-spacing * 0.5) 0;
  padding: $base-spacing;
}

input,
label,
select {
  //display: block;
  font-family: $form-font-family;
  font-size: $form-font-size;
}

.block-editor input[type=checkbox], .components-modal__frame input[type=checkbox] {
  display: block;
}

label {
  font-weight: bold;
  margin-bottom: $base-spacing * 0.25;
  &.required:after {
    content: "*";
  }
  abbr {
    display: none;
  }
}

textarea,
#{$all-text-inputs},
select[multiple=multiple] {
  @include box-sizing(border-box);
  @include transition(border-color);
  background-color: white;
  border-radius: $form-border-radius;
  border: 1px solid $form-border-color;
  box-shadow: $form-box-shadow;
  font-family: $form-font-family;
  font-size: $form-font-size;
  margin-bottom: $base-spacing * 0.5;
  padding: math.div($base-spacing, 3) math.div($base-spacing, 3);
  width: 100%;
  &:hover {
    border-color: $form-border-color-hover;
  }
  &:focus {
    border-color: $form-border-color-focus;
    box-shadow: $form-box-shadow-focus;
    outline: none;
  }
}

textarea {
  resize: vertical;
}

input[type="search"] {
  @include appearance(none);
}

input[type="checkbox"],
input[type="radio"] {
  display: inline-block;
  margin-right: $base-spacing * 0.25;
}

input[type="file"] {
  padding-bottom: $base-spacing * 0.5;
  width: 100%;
}

select {
  margin-bottom: $base-spacing;
  max-width: 100%;
  width: auto;
}

body .gform_wrapper .gform_body .gform_fields .gfield .gfield_label .gfield_required {
  color: #fff
}

body .gform_wrapper .gform_body .gform_fields .gfield .gfield_label {
  margin: 0;
}

body .gform_wrapper .gform_body .gform_fields li.field_sublabel_below {
  margin-bottom: 1em;
}

.gform_body {
  span input {
    margin-bottom: 0 !important;
  }
  span label {
    font-size: .7em !important;
    font-weight: 300;
  }
  .validation_message {
    background: rgba(251, 35, 35, 0.76);
    font-size: .7em;
    color: white;
    border-radius: .5em;
    padding: .25em;
  }
  .LV_invalid_field {
    border-color: red;
  }
}

.gform_body ul {
  margin: 0;
  
  li {
    list-style: none;
    margin-left: 0;
    
    li {
      margin-left: 0;
      
      label {
        display: inline;
      }
    }
  }
}


.form-block input:not([type=submit]) {
  color: $dark-gray;
}

.form-block a:not(.button) {
  color: $base-color-light;
  text-decoration: none;
  background-image: linear-gradient(currentColor,currentColor);
  background-size: 100% 1px;
  background-position: center bottom 29%;
  background-repeat: no-repeat;
  padding: 0.75em 0;
  &:hover {
    color: #d4d4d4;
  }
}

.gfield {
    .gfield_description, .gsection_description {
      font-size: .8em;
    }
}

.roi-calculator-form span.ginput_product_price_label {
  display: none;
}