@import "trade-gothic";
body {
  @include font-feature-settings("kern", "liga", "pnum");
  -webkit-font-smoothing: antialiased;
  background-color: $base-background-color;
  color: $base-font-color;
  font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: $base-line-height;

  @include media(screen and min-width $first-breakpoint-value) {
    font-size: ($base-font-size * 1.2);
  }
}
body#tinymce {
  padding:1em !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $header-font-family;
  line-height: $header-line-height;
  margin: .5em 0 .25em 0;
}

h1 {
  font-size: 2rem;
  @media screen and ( min-width: 600px ) {
    font-size: $h1-font-size;
  }
}

h2 {
  font-size: 1.5rem;
  @media screen and ( min-width: 600px ) {
    font-size: $h2-font-size;
  }

  &.caps {
    font-size: 1em;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 400;
  }
}

h3 {
  font-size: $h3-font-size;
  margin: 1em 0 .5em 0;
}

h4 {
  font-size: $h4-font-size;
  margin: 0 0 .5em 0;
  font-weight: 700;
}

h5 {
  font-size: $h5-font-size;
}

h6 {
  font-size: $h6-font-size;
  margin: 1em 0 0 0;
}

p {
  margin: 0 0 ($base-spacing * 0.5);
}

a {
  @include transition(color 0.1s linear);
  color: inherit;
  text-decoration: none;
  &:hover {
    color: inherit;
  }
  &:active,
  &:focus,
  &:visited {
    color: inherit;
  }
  #secondary & {
    text-decoration: none;
    background-image: linear-gradient(currentColor,currentColor);
    background-size: 100% 1px;
    background-position: center bottom 29%;
    background-repeat: no-repeat;
    padding: 0.75em 0;
  }
}

hr {
  border-bottom: $base-border;
  border-left: none;
  border-right: none;
  border-top: none;
  margin: $base-spacing 0;
}

sup {
  vertical-align: super;
  font-size: .5em;
  font-style: normal;
  padding-left: 5px;
}

strong,
b {
  font-weight: 700;
}

em,
i {
  font-style: italic;
}
figure.quote {
  background: rgba(194, 194, 194, 0.1);
  padding: 1em 3.5em;
  margin: 1em 0;
}
blockquote {
  margin: 0;
  padding: 0;
  quotes: "\201C""\201D";
  font-size: 1em;
  @include media(min-width $second-breakpoint-value) {
    //margin-left: 1.8em;
    font-size: 1em;
  }
  &.wp-block-quote {
    margin-left: -.5em;
  }
  &:before {
    color: #ccc;
    color: var(--ugb-text-color);
    content: open-quote;
    font-size: 2.3em;
    line-height: 0.1em;
    margin-right: 0.25em;
    position: relative;
    display: block;
    top: 0.4em;
    left: -0.5em;
    z-index: 1;
    opacity: .3;

    .dark-text & {
      color: #333;
    }
  }
  p:last-of-type:after {
    content: close-quote;
    opacity: .3;
  }
  footer {
    padding-left: 3em;
  }
}

cite, .quote figcaption {
  font-style: italic;
  font-size: .75em;
  line-height: 1.5em;
  display: block;
  margin-top: 1em;
}

pre {
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  margin-bottom: 24px;
  max-width: 100%;
  overflow: auto;
  padding: 12px;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.font-size-medium {
  font-size: .75em;
}
